@import "style/mixins.module.scss";

.menu {
    display: flex;
    flex-direction: column;

    @include respond-to("tablet") {
        flex-direction: row;
    }

    &_type-germany {
        & .link-text {
            font-family: Inter, sans-serif;
            font-weight: 400;
        }
    }
}

.link {
    display: flex;
    align-items: center;
    padding: 16px 0;

    @include respond-to("mobile") {
        flex-direction: row-reverse;
        justify-content: space-between;
        border-bottom: 0.5px solid rgba(66, 78, 88, 0.1);
    }
    @include respond-to("tablet") {
        flex-direction: row;
    }

    & + & {
        @include respond-to("tablet") {
            margin-left: 30px;
        }

        @include respond-to("laptop") {
            margin-left: 50px;
        }
    }

    @include respond-to("tablet") {
        &_light {
            @include color("gray", "0");
        }
    }
}

.link-text {
    margin: 0;
    font-family: Inter, sans-serif;
    font-size: 17px;
    font-weight: 500;
    line-height: 24px;

    @include respond-to("tablet") {
        margin-left: 15px;
    }
}
