@import "style/mixins.module.scss";

.root {
    display: flex;
    padding: 24px 0 80px;

    @include respond-to("mobile") {
        flex-direction: column;
    }

    @include respond-to("laptop") {
        display: grid;
        grid-template-columns: repeat(8, 1fr);
        grid-gap: 24px;
        padding-bottom: 24px;
        border-top: 0.5px solid rgba(66, 78, 88, 0.1);
    }

    @include respond-to("laptop-L") {
        grid-template-columns: repeat(12, 1fr);
        grid-gap: 40px;
    }

    &_light {
        .logo,
        .link,
        .privacy {
            @include color("gray", "-5");
        }

        .privacy {
            @include respond-to("mobile") {
                border-top: 0.5px solid get_color("opacities", "white-4");
            }
        }

        @include respond-to("laptop") {
            border-top: 0.5px solid get_color("opacities", "white-4");
        }

        @include respond-to("mobile") {
            .link {
                border-top: 0.5px solid get_color("opacities", "white-4") !important;
            }
        }
    }

    &_type-germany {
        .logo {
            transform: none;
        }
    }
}

.logo {
    z-index: 1;
    display: block;
    align-self: start;
    transform: scale(0.7) translateX(-22%) translateY(25%);

    @include respond-to("mobile") {
        margin-bottom: 40px;
    }

    @include respond-to("laptop") {
        grid-row: 1;
        grid-column: 1 / span 3;
        transform: scale(0.8) translateX(-12%) translateY(-10%);
    }

    &__icon {
        width: 100%;
    }
}

.container-links {
    @include respond-to("mobile") {
        margin-bottom: 40px;
    }

    @include respond-to("laptop") {
        display: grid;
        grid-column: 4 / span 5;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr 1fr;
        grid-gap: 20px;
    }

    @include respond-to("laptop-L") {
        grid-column: 6 / span 7;
    }
}

.link {
    display: flex;
    font-size: 17px;
    line-height: 24px;
    font-weight: 500;

    @include respond-to("mobile") {
        justify-content: space-between;
        padding: 16px 0;

        & + & {
            border-top: 0.5px solid rgba(66, 78, 88, 0.1);
        }
    }

    @include respond-to("laptop") {
        flex-direction: row-reverse;
        justify-content: flex-end;

        &_wa {
            order: 3;
        }

        &_email {
            order: 5;
        }

        &_site {
            order: 2;
        }

        &_covid {
            order: 4;
        }
    }

    &__icon {
        width: 24px;
        height: 24px;
        justify-content: center;

        @include respond-to("laptop") {
            margin-right: 14px;
        }
    }
}

.privacy {
    align-self: start;

    @include color("primary", "blue");

    @include respond-to("mobile") {
        width: 100%;
        order: 3;
        padding: 16px 0;
        border-top: 0.5px solid rgba(66, 78, 88, 0.1);
    }

    @include respond-to("laptop") {
        grid-row: 1;
        grid-column: 1 / span 3;
        padding-top: 36px;
    }

    @include respond-to("laptop-L") {
        padding-top: 40px;
    }
}
