@import "style/mixins.module.scss";

.calendly {
    -webkit-overflow-scrolling: touch;

    width: 100%;
    height: 100%;
}

.calendly-wrapper {
    min-width: 296px;

    &_is-loaded {
        height: 1100px;
    }

    &_has-error {
        height: auto;
    }
}

@media (max-width: 697px) {
    .calendly-wrapper {
        &_is-loaded {
            height: 2200px;
        }
    }
}

.calendly-error {
    display: flex;
    flex-direction: column;
    align-items: center;

    text-align: center;
}

.calendly-error-content {
    max-width: 480px;
}

.calendly-error-description {
    margin-bottom: 80px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        margin-bottom: 64px;
        font-size: 22px;
        line-height: 32px;
    }
}

.calendly-error-icon {
    margin-bottom: 35px;
}

.calendly-error-contacts {
    margin-bottom: 50px;

    @include respond-to("laptop") {
        & ul {
            display: flex;
            justify-content: space-between;
        }
    }
}

.icon-loading {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
