@import "style/mixins.module.scss";

.item {
    $item: &;
    margin-bottom: 64px;

    &_theme-light {
        #{$item}__title {
            @include color("opacities", "white-7");
        }
        #{$item}__description {
            @include color("gray", "0");
        }
    }

    @include respond-to("mobile") {
        display: flex;
        flex-direction: column-reverse;
    }

    @include respond-to("laptop") {
        margin-bottom: 0;
    }

    &__image-wrapper,
    &__picture {
        position: relative;

        @include respond-to("mobile") {
            margin-bottom: 40px;
        }

        &_mobile-only {
            display: block;

            @include respond-to("laptop") {
                display: none;
            }
        }

        &_desktop-only {
            display: none;

            @include respond-to("laptop") {
                display: block;
            }
        }

        @include respond-to("laptop") {
            width: calc(50% + 40px);
            margin-right: -40px;
            margin-left: 0;
        }

        @include respond-to("laptop-L") {
            width: calc(50% + 8vw);
            margin-right: -8vw;
        }

        @include respond-to("laptop-XL") {
            width: calc(50% + 196px);
            margin-right: -196px;
        }
    }

    &__image-wrapper {
        &_is-full-width {
            @include respond-to("mobile") {
                width: calc(100% + 48px);
                margin: 0 -24px 40px;
            }
        }

        @include respond-to("mobile") {
            margin-bottom: 40px;
        }

        & .item__picture {
            margin-bottom: 0;
        }
    }

    &__image,
    &__gif,
    &__video {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__content {
        @include respond-to("laptop") {
            width: 50%;
            padding-top: 40px;
            padding-right: 40px;
        }

        @include respond-to("laptop-L") {
            padding-top: 80px;
            padding-right: 80px;

            &_with-points {
                padding-top: 64px;
            }
        }
    }

    &__title {
        margin-bottom: 16px;

        &_no-description {
            margin-bottom: 24px;
        }

        @include respond-to("laptop") {
            margin-bottom: 16px;

            &_no-description {
                margin-bottom: 24px;
            }
        }

        @include respond-to("laptop-L") {
            &_no-description {
                margin-bottom: 40px;
            }
        }
    }

    &__description {
        margin: 0;
        font-size: 17px;
        line-height: 24px;

        @include respond-to("laptop") {
            font-size: 22px;
            line-height: 32px;
        }
    }

    &__points {
        display: flex;
        padding: 24px 0 40px;

        @include respond-to("mobile") {
            flex-direction: column;
        }

        @include respond-to("laptop") {
            padding: 40px 0 80px;
        }
    }

    &__ticker {
        width: calc(100% - 48px);
        left: 24px;
    }

    @include respond-to("laptop") {
        display: flex;
        flex-direction: row;

        &:nth-of-type(even) {
            flex-direction: row-reverse;
        }

        &:nth-of-type(even) &__picture {
            margin-right: 0;
            margin-left: -40px;
        }

        &:nth-of-type(even) &__content {
            padding-right: 0;
            padding-left: 40px;
        }
    }

    @include respond-to("laptop-L") {
        &:nth-of-type(even) &__picture {
            margin-right: 0;
            margin-left: -8vw;
        }
    }

    @include respond-to("laptop-XL") {
        &:nth-of-type(even) &__picture {
            margin-left: -196px;
        }
    }
}
