@import "style/mixins.module.scss";

.root {
    position: relative;
    bottom: -3px;
    display: inline;
    margin-bottom: 0;

    font-size: 17px;
    line-height: 24px;
    font-weight: 700;
    letter-spacing: 0.05em;

    color: #eb5e5b;

    &_gray {
        @include color("gray", "-2");
    }

    &:after {
        content: "";
        position: absolute;
        height: 3px;
        width: 100%;
        top: 47%;
        left: 0;

        transform: rotate(-10deg);
        background: #eb5e5b;
    }

    &_white:after {
        border: 1px solid #fff;
    }

    &_gray:after {
        border: 1px solid get_color("gray", "-2");
        background: get_color("gray", "-2");
    }

    &_blue:after {
        border: 1px solid #013a79;
    }

    @include respond-to("laptop") {
        font-size: 22px;
        line-height: 32px;
    }
}
