@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;
}

.title {
    margin-bottom: 16px;
}

.list {
    padding: 0;
    margin: 0;
    list-style: none;

    &__item {
        @include respond-to("mobile") {
            &:not(:last-child) {
                border-bottom: 0.5px solid rgba(66, 78, 88, 0.1);
            }
        }
    }
}

.link {
    @include color("gray", "-4");

    position: relative;
    display: flex;
    justify-content: space-between;
    padding: 16px 0;

    font-size: 17px;
    line-height: 24px;
    font-weight: 500;

    @include respond-to("laptop") {
        flex-direction: row-reverse;
        justify-content: flex-end;
    }
}

.icon {
    @include respond-to("laptop") {
        margin-right: 16px;
    }
}
