@import "style/mixins.module.scss";

.container {
    width: 100%;
    margin-bottom: 64px;
    padding: 0 24px;

    @include respond-to("laptop") {
        margin-bottom: 24px;
        padding: 0 40px;
    }

    @include respond-to("laptop-L") {
        width: 1208px;
        margin: 0 auto 120px;
        padding: 0;
    }
}

.main {
    padding: 24px 24px 64px;
    background-color: var(--color-light-gray);

    @include respond-to("laptop") {
        padding-right: 40px;
        padding-left: 40px;
    }

    @include respond-to("laptop-L") {
        padding-right: 0;
        padding-left: 0;
    }

    // @include respond-to("laptop-L") {
    //     float: left;
    //     width: 50%;
    //     padding: 0;
    //     background-color: transparent;
    // }

    &_margin {
        margin-bottom: 104px;

        @include respond-to("laptop") {
            margin-bottom: 87px;
        }
    }

    &__title {
        &_margin {
            margin-bottom: 24px;

            @include respond-to("laptop") {
                margin-bottom: 40px;
            }
        }
    }

    &__subtitle {
        margin-bottom: 8px;

        @include respond-to("laptop") {
            margin-bottom: 16px;
        }
    }

    &__button {
        max-width: 327px;
    }
}

.logo {
    display: block;

    margin-bottom: 64px;

    @include respond-to("laptop-L") {
        display: none;
    }

    &__icon {
        transform: scale(0.7) translateX(-20%) translateY(-10%);

        @include respond-to("laptop") {
            transform: scale(0.8) translateX(-12%) translateY(-18%);
        }
    }
}

.header {
    display: none !important;

    @include respond-to("laptop-L") {
        display: flex !important;
    }

    @include respond-to("laptop-XL") {
        padding-right: 140px !important;
        padding-left: 140px !important;
    }
}

.content {
    &_with-paddings {
        padding-top: 70px;
        padding-bottom: 70px;

        @include respond-to("laptop") {
            padding-top: 104px;
            padding-bottom: 128px;
        }
    }

    @include respond-to("laptop") {
        margin: 0 auto;
        text-align: center;
    }

    @include respond-to("laptop-L") {
        max-width: 944px;
        text-align: center;
        padding-top: 40px;
        padding-bottom: 100px;
    }
}

.booking {
    position: relative;
    max-width: 1208px;
    min-height: 617px;
    margin: 0 auto 104px;
    background-color: get_color("gray", "0");
    box-shadow: 0px 6px 14px -6px rgba(24, 39, 75, 0.12),
        0px 10px 32px -4px rgba(24, 39, 75, 0.1);
    text-align: center;

    @include respond-to("laptop") {
        min-height: 886px;
        margin-top: -40px;
        margin-bottom: 80px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 160px;
    }

    &.error {
        min-height: auto;

        @include respond-to("mobile") {
            height: auto;
            padding: 0 24px;
        }

        @include respond-to("laptop") {
            height: 617px;
        }
    }
}

.icon-loading {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
}

.calendar {
    padding: 55px 0 0px;

    @include respond-to("laptop") {
        padding-top: 40px;
        padding-bottom: 50px;
    }
    // @include respond-to("laptop-L") {
    //     float: left;
    //     width: 50%;
    //     min-height: 864px;
    //     margin-bottom: 160px;
    //     padding-top: 147px;
    //     padding-bottom: 76px;
    //     background-color: var(--color-light-gray);
    //     box-shadow: none;
    // }

    &__head {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        max-width: 584px;
        margin: 0 auto;
        padding: 0 24px;

        @include respond-to("laptop") {
            margin-bottom: -40px;
        }
    }
}

.select-city {
    width: 100%;
    max-height: 617px;
    padding: 0 24px;
    overflow-y: auto;

    @include respond-to("laptop") {
        max-height: 886px;
    }
}

.calendly {
    display: grid;
    height: auto;
    min-height: 650px;

    @include respond-to("laptop") {
        min-height: 700px;
    }

    &.error {
        min-height: auto;
    }
}

.grid {
    margin-bottom: 80px;

    @include respond-to("laptop-L") {
        margin-bottom: 160px;
    }

    &__title {
        max-width: none;
        margin-bottom: 64px;

        @include respond-to("laptop") {
            margin-bottom: 80px;
        }

        @include respond-to("laptop-L") {
            margin-bottom: 160px;
        }
    }
}

.advantages-block {
    margin-bottom: 40px;

    @include respond-to("laptop") {
        margin-bottom: 80px;
    }

    &__title {
        margin-bottom: 8px;
        letter-spacing: -0.02em;

        @include respond-to("laptop") {
            max-width: 823px;
            margin-bottom: 16px;
        }
    }

    &__subtitle {
        font-size: 17px;
        margin-bottom: 24px;

        @include respond-to("laptop") {
            max-width: 524px;
            margin-bottom: 64px;
            font-size: 22px;
            line-height: 32px;
        }

        @include respond-to("laptop-L") {
            max-width: 524px;
            font-size: 22px;
            line-height: 32px;
        }
    }

    &__list {
        @include respond-to("laptop") {
            display: flex;
        }
    }

    &__item {
        display: flex;
        margin-bottom: 24px;

        @include respond-to("laptop") {
            flex-direction: column;
            width: calc((100% - 48px) / 3);
            font-size: 22px;
            line-height: 32px;

            & + & {
                margin-left: 24px;
            }

            p {
                padding-left: 0;
            }
        }

        @include respond-to("laptop-L") {
            width: calc((100% - 80px) / 3);
            font-size: 22px;
            line-height: 32px;

            & + & {
                margin-left: 40px;
            }
        }
    }

    &__icon {
        margin-right: 16px;

        @include respond-to("laptop") {
            margin-bottom: 16px;
        }
    }
}

.cta {
    margin-bottom: 80px;
    padding: 70px 0;
    background-color: var(--color-light-gray);
    text-align: center;

    @include respond-to("laptop-L") {
        margin-bottom: 160px;
        padding: 108px 0;
    }

    &__title {
        margin-bottom: 40px;

        @include respond-to("laptop-L") {
            max-width: 715px;
            margin: 0 auto 40px;
        }
    }
}
