@import "style/mixins.module.scss";
@import "./Animate.module.scss";

.root {
    position: absolute;
    width: 100%;
    z-index: 1;
    font-family: inherit;
    font-weight: 700;
    font-size: 10px;
    line-height: 15px;

    @include color("gray", "0");
    list-style: none;
    overflow: hidden;

    @include respond-to("laptop") {
        display: none;
    }

    &_position {
        &_top {
            top: 8px;

            .ticker-item {
                &:nth-child(odd) {
                    opacity: 0.5;
                }
            }
        }

        &_bottom {
            bottom: 8px;

            .ticker-item {
                &:nth-child(even) {
                    opacity: 0.5;
                }
            }
        }
    }
}

.ticker {
    display: flex;
    align-items: center;
    padding-right: 100%;
    margin: 0;
    list-style: none;
    white-space: nowrap;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    animation-name: ticker;
    animation-duration: $duration;
}

.ticker-item {
    position: relative;
    margin-right: 40px;
}
