@import "style/mixins.module.scss";

.root {
    display: flex;
    align-items: center;

    &_reverse {
        flex-direction: column;
        align-items: flex-start;
        width: calc(50% - 12px);
        margin-bottom: 24px;

        @include respond-to("laptop") {
            margin-bottom: 40px;
        }
    }

    & + & {
        @include respond-to("mobile") {
            margin-top: 24px;

            &_reverse {
                margin-top: 0;
            }
        }

        @include respond-to("laptop") {
            margin-left: 24px;

            &_reverse {
                margin-left: 0;
            }
        }
    }

    @include respond-to("laptop") {
        flex-direction: column;
        align-items: flex-start;
        width: calc(100% / 3);

        &_reverse {
            flex-direction: row;
            justify-content: space-between;
            width: 42%;
        }
    }
}

.theme {
    &_light {
        .icon {
            color: transparent;
        }

        .text {
            @include color("gray", "0");
        }
    }
}

.icon {
    @include respond-to("mobile") {
        margin-right: 16px;

        &_reverse {
            margin-right: 0;
            margin-bottom: 8px;
        }
    }

    @include respond-to("laptop") {
        margin-bottom: 16px;

        &_reverse {
            margin-right: 24px;
            margin-bottom: 0;
        }
    }
}

.text {
    margin: 0;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        &_reverse {
            font-size: 22px;
            line-height: 32px;
        }
    }

    @include respond-to("laptop-L") {
        font-size: 22px;
        line-height: 32px;
    }
}
