@import "style/mixins.module.scss";

.root {
    z-index: var(--z-index-header);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 56px;

    padding-left: 24px;
    padding-right: 24px;

    @include respond-to("tablet") {
        padding-left: 40px;
        padding-right: 40px;
    }

    @include respond-to("laptop-L") {
        padding-left: 80px;
        padding-right: 80px;
    }

    @include respond-to("laptop-XL") {
        padding-left: 40px;
        padding-right: 40px;
    }

    &_absolute {
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
    }

    &_type-germany {
        .logo {
            transform: none;
        }
    }
}

.logo {
    display: block;
}

.logo-icons {
    display: flex;
    align-items: center;
    width: 100%;
}

.logo-icon {
    width: 140px;

    @include respond-to("laptop") {
        width: 160px;
    }
}

.logo-teens-icon {
    margin-left: 5px;
    color: transparent;
    width: 87px;

    @include respond-to("laptop") {
        width: 100px;
    }
}

.logo_light {
    color: get_color("gray", "-5");
}

.logo_brand {
    color: #2444e6;
}

.desktop-menu {
    display: none;

    @include respond-to("tablet") {
        display: inherit;
    }
}

.button-fixed-wrapper {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 0 117px 16px 24px;
    mix-blend-mode: normal;
    z-index: var(--z-index-fixed-buttons);

    @include respond-to("tablet") {
        display: none;
    }

    &_version-germany {
        padding: 0 95px 16px 24px;
    }
}

.button-fixed {
    padding: 16px 14px;
}
