$search-hover-bg-color: rgba(238, 243, 244, 0.4);
$search-border-color: rgba(66, 78, 88, 0.1);
$placeholder-color: #abb4bb;

.root {
    position: relative;
}

.search {
    width: 100%;
    padding: 20px 20px 20px 36px;
    border: none;
    border-bottom: 1px solid $search-border-color;

    font-size: 17px;
    line-height: 24px;

    transition: all 0.3s;

    outline: none;

    &:active,
    &:focus {
        border-bottom: 1px solid #45a1f8;
    }

    &::placeholder {
        font-size: 17px;
        line-height: 24px;
        color: $placeholder-color;
    }
}

.icon {
    position: absolute;
    top: 20px;
    left: 0;
}

