@import "style/mixins.module.scss";

.content {
    display: flex;
    margin-bottom: 16px;
    padding-right: 8px;
    padding-left: 24px;
    width: max-content;

    @include respond-to("laptop") {
        margin-bottom: 40px;
        padding-left: 40px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 64px;
        padding-left: 0;
    }
}

.tab {
    @include color("gray", "-4");
    @include normalizedButton();

    margin: 0;
    padding: 8px 16px;
    font-weight: 500;
    font-size: 17px;
    line-height: 24px;
    font-family: Inter, sans-serif;
    border-radius: 2px;
    cursor: pointer;

    transition: background-color ease-in 200ms;

    &_active {
        @include color("primary", "red");

        background-color: var(--color-lavendar-blush);
    }
}
