@import "style/mixins.module.scss";

$font-color: #424e58;

.link {
    @include normalizedButton;

    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 20px 0;
    font-size: 17px;
    line-height: 24px;
    font-weight: 500;
    font-family: Inter, sans-serif;
    cursor: pointer;

    color: $font-color;

    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
}

