@import "style/mixins.module.scss";

.root {
    &_theme-light {
        @include color("gray", "0");

        .monthly {
            border-bottom: 1px solid get_color("opacities", "white-4");
        }

        .amount,
        .interest {
            @include color("gray", "0");
        }

        .price__old {
            @include color("gray", "0");

            &:after {
                background-color: get_color("gray", "0");
                border: 1px solid get_color("primary", "red");
            }
        }

        .action {
            display: none;
            @include color("primary", "red");
            background-color: get_color("gray", "0");

            &:hover {
                @include color("primary", "red");
                background-color: get_color("gray", "0");
            }

            @include respond-to("laptop") {
                display: block;
            }
        }
    }
}

.row {
    display: flex;
    width: 100%;
    margin-bottom: 24px;
}

.monthly {
    border-bottom: 1px solid rgba(66, 78, 88, 0.1);
}

.payment-type {
    width: 30%;
    min-width: 30%;
    padding-right: 19px;
    font-family: Inter, sans-serif;
    font-weight: 700;
    font-size: 14px;
    line-height: 20px;

    @media (min-width: 375px) {
        font-size: 17px;
        line-height: 24px;
    }

    @include respond-to("laptop") {
        width: 25%;
        min-width: 25%;
    }

    @include respond-to("laptop-L") {
        padding-right: 68px;
        font-size: 22px;
        line-height: 32px;
    }
}

.price {
    display: flex;
    flex-direction: column;
    margin-left: 16px;

    @include respond-to("laptop-L") {
        margin-left: 24px;
    }

    &__new {
        display: flex;
    }

    &__old {
        font-size: 17px;
        line-height: 24px;
        font-weight: bold;
        font-family: Inter, sans-serif;
        letter-spacing: normal;
        @include color("gray", "-2");

        &:after {
            background-color: get_color("gray", "-2");
            border: 1px solid get_color("gray", "0");
        }

        @include respond-to("laptop-L") {
            font-size: 22px;
            line-height: 32px;
        }
    }
}

.amount {
    margin-right: 8px;

    font-size: 36px;
    line-height: 40px;
    font-weight: bold;
    font-family: Inter, sans-serif;

    @include respond-to("laptop-L") {
        font-size: 56px;
        line-height: 64px;
    }

    &_red {
        @include color("primary", "red");
    }
}

.column {
    display: flex;
    flex-direction: column;
    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop-L") {
        font-size: 22px;
        line-height: 30px;
    }
}

.interest {
    @include color("primary", "red");

    margin: 0;
    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop-L") {
        font-size: 17px;
        line-height: 24px;
    }
}

.legal,
.prepayment {
    font-size: 13px;
    line-height: 20px;

    @include respond-to("laptop-L") {
        font-size: 17px;
        line-height: 24px;
    }
}

.prepayment {
    margin: 5px;
}

.legal {
    margin-bottom: 24px;

    &_price {
        padding-top: 16px;

        @include respond-to("laptop-L") {
            margin-bottom: 48px;
            padding-top: 24px;
        }
    }
}
