@import "style/mixins.module.scss";

.root {
    position: relative;
    margin-bottom: 80px;

    @include respond-to("laptop-L") {
        margin-bottom: 160px;
    }

    :global .react-multi-carousel-dot-list {
        display: flex;
        margin: 16px auto 0;
        list-style: none;
    }

    :global .react-multi-carousel-dot {
        width: 6px;
        height: 6px;
        margin-right: 4px;
        border: 1px solid get_color("gray", "-4");
        border-radius: 50%;

        &--active {
            background: get_color("gray", "-4");
        }

        button {
            @include normalizedButton;
        }
    }
}

.list {
    position: relative;

    display: flex;
    flex-wrap: wrap;

    overflow: hidden;

    @include respond-to("laptop-L") {
        width: 100%;
    }
}

.container {
    display: flex;
    flex-direction: unset;
    margin: 0;
    padding: 0 24px;
}

.item {
    box-shadow: 0px 0px 1px 8px white;
    list-style-type: none;
}

.header {
    display: flex;
    flex-direction: column;

    @include respond-to("laptop-L") {
        max-width: 1208px;
        margin: 0 auto;
        flex-direction: row;
    }
}

.title {
    margin-bottom: 24px;
    padding: 0 24px;

    @include respond-to("laptop") {
        margin-bottom: 32px;
        padding: 0 40px;
    }

    @include respond-to("laptop-L") {
        padding-right: 32px;
        padding-left: 0;
        line-height: 40px;
        white-space: nowrap;
    }
}

.tabs {
    @include respond-to("laptop-L") {
        width: auto;
    }
}

.arrow {
    position: absolute;
    z-index: 10;
    width: 56px;
    top: 50%;
    transform: translateY(-50%);
    height: 56px;
    background-color: #fff;
    border-radius: 2px;
    border: none;
    padding: 0;
    outline: none;
}

.container-arrow {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%);
    width: 100%;
    display: flex;
    justify-content: flex-end;
}

.arrow-left {
    left: 28px;
}

.arrow-right {
    right: 28px;
}

.arrow-icon {
    width: 24px;
    height: 24px;
}

.arrow-icon-right {
    transform: rotate(180deg);
}

.image {
    object-fit: cover;
    display: block;
    height: 240px;
    width: 100%;

    @include respond-to("laptop") {
        height: 400px;
    }

    @include respond-to("laptop-L") {
        height: 520px;
    }
}
